import React from 'react';
import CategoryGridView from "../VisualElements/CategoryGridView";
import categoriesImageList from "../../data/ImageListsForCategories/categoriesImageList"

export const MainPage = () => {
    return (
        <div>
            <div>
                <h1 style={{"fontSize": "calc(24px + 16 * (120vw / 1280))", "textAlign": "center"}}>Группы товаров:</h1>
                <CategoryGridView CategoryList={categoriesImageList}/>
            </div>
        </div>
    )
}
