import React from 'react';
import CategoryGridView from "../VisualElements/CategoryGridView";
import brandsSubCategoryImageList from "../../data/ImageListsForCategories/brandsSubCategoryImageList";

export const BrandsPage = () => {
    return (
        <div>
            <h1 style={{"fontSize": "calc(24px + 16 * (120vw / 1280))", "textAlign": "center"}}>Бренды</h1>
            <CategoryGridView CategoryList={brandsSubCategoryImageList}/>
        </div>
    );
};
